import {readUIFielObjectById} from './../utility.js';
import {_CONSTANTS} from './../constants.js';
import {locateGraphNodes} from './../graphLib/graph';

export function collapseAllSiblings (state, {node, allNodes, replotGraph}) {
    const selectedNodeId = node.loc_id;
    const collapseSiblings = () => {
        const parentId = selectedNodeId.slice(0,-2);
        const parentNode = allNodes[parentId];
        const siblings = parentNode.children;
        siblings.forEach(_sb => {
            if (_sb !== selectedNodeId && ![_CONSTANTS.NODE_TYPES.END_VALUE, _CONSTANTS.NODE_TYPES.KEY].includes(allNodes[_sb].type)) {
                allNodes[_sb].expanded = false
            }
        })
    }
    if ((node.type !== _CONSTANTS.NODE_TYPES.END_VALUE)) {
        collapseSiblings();
        locateGraphNodes(node, allNodes);
        const _target = readUIFielObjectById(node.loc_id);
        _target.setAttribute('stroke-width',8);
        setTimeout(()=>{
            readUIFielObjectById(node.loc_id).scrollIntoView({behavior: "smooth",block: "start", inline: "nearest"});
        }, 10);
        setTimeout(()=>{
            _target.setAttribute('stroke-width',2);
        }, 1000);
    }
    if (state.UI_STATE === 700) {
        state.COMPARE_CACHE['0__1'].ALL_NODES_COMPARED = allNodes;
    } else {
        state.JSON_SET[state.ACTIVE_JSON].GRAPH = allNodes;
    }    
    return state;
};

