export function expandCollapseNode(state, {node, allNodes, replotGraph}) {
    if ((node.type === _CONSTANTS.NODE_TYPES.OBJECT || node.type === _CONSTANTS.NODE_TYPES.ARRAY)) {
        const _currentStatus = node.expanded;
        node.expanded = !node.expanded;
        locateGraphNodes(node, allNodes);
        const _target = readUIFielObjectById(node.loc_id);
        if (!_currentStatus) {
            _target.setAttribute('stroke-width',8);
        }
        setTimeout(()=>{
            readUIFielObjectById(node.loc_id).scrollIntoView({behavior: "smooth",block: "start", inline: "nearest"});
        }, 10);
        setTimeout(()=>{
            if (!_currentStatus) {
                _target.setAttribute('stroke-width',2);
            }
        }, 1000);
    }
    if (state.UI_STATE === 700) {
        state.COMPARE_CACHE['0__1'].ALL_NODES_COMPARED = allNodes;
    } else {
        state.JSON_SET[state.ACTIVE_JSON].GRAPH = allNodes;
    }
    return state;
};

export function focusParent(node) {
    const selectedNodeId = node.loc_id;
    const parentNodeId = selectedNodeId.slice(0,-2);
    const _target = readUIFielObjectById(parentNodeId);
    _target.firstChild.setAttribute('stroke-width',8);
    setTimeout(()=>{
        readUIFielObjectById(parentNodeId).scrollIntoView({behavior: "smooth",block: "start", inline: "nearest"});
    }, 10);
    setTimeout(()=>{
        _target.firstChild.setAttribute('stroke-width',2);
    }, 2000);
};

export function pathToRoot(node, ALL_NODES) {
    const selectedNodeId = node.loc_id.split('.');
    const path = [];
    selectedNodeId.map((_id, idx) => {
        const currId = selectedNodeId.slice(0, idx+1).join('.');
        const nodeKey = ALL_NODES[currId].text_value;
        path.push(nodeKey);
    })
    path.shift();
    alert(" Path to Root Object is : " + path.join(' . '))
};

export function highlightConnectedNodes(targetElem) {
    const _oldLineStrokeWidth = targetElem.target.getAttribute("stroke-width")
    const _oldLineStrokeColor = targetElem.target.getAttribute("stroke");
    const _id = targetElem.srcElement.id;
    const [_source, _target, _type] = _id.split('-');

    const _srcNode = readUIFielObjectById(_source);
    const _oldSrcNodeStrokeWidth = _srcNode.firstChild.getAttribute("stroke-width")
    const _oldSrcNodeStrokeColor = _srcNode.firstChild.getAttribute("stroke")
    _srcNode.firstChild.setAttribute('stroke-width',3);
    _srcNode.firstChild.setAttribute('stroke','black');

    const _trgtNode = readUIFielObjectById(_target);
    const _oldTrgtNodeStrokeWidth = _trgtNode.firstChild.getAttribute("stroke-width")
    const _oldTrgtNodeStrokeColor = _trgtNode.firstChild.getAttribute("stroke")
    _trgtNode.firstChild.setAttribute('stroke-width',3);
    _trgtNode.firstChild.setAttribute('stroke','black');

    if (_type === 's'){
        const _line = readUIFielObjectById(_id);
        _line.setAttribute('stroke-width',4);
        _line.setAttribute('stroke','black');
        setTimeout(()=>{
            _srcNode.firstChild.setAttribute('stroke-width', _oldSrcNodeStrokeWidth);
            _srcNode.firstChild.setAttribute('stroke', _oldSrcNodeStrokeColor);
            _trgtNode.firstChild.setAttribute('stroke-width', _oldTrgtNodeStrokeWidth);
            _trgtNode.firstChild.setAttribute('stroke', _oldTrgtNodeStrokeColor);
            _line.setAttribute('stroke-width', _oldLineStrokeWidth);
            _line.setAttribute('stroke', _oldLineStrokeColor);
        }, 5000);
    }else{
        const _vLine = readUIFielObjectById(_source + '-' + _target + '-v');
        _vLine.setAttribute('stroke-width',4);
        _vLine.setAttribute('stroke','black');
        const _hLine = readUIFielObjectById(_source + '-' + _target + '-h');
        _hLine.setAttribute('stroke-width',4);
        _hLine.setAttribute('stroke','black');
        setTimeout(()=>{
            _srcNode.firstChild.setAttribute('stroke-width', _oldSrcNodeStrokeWidth);
            _srcNode.firstChild.setAttribute('stroke', _oldSrcNodeStrokeColor);
            _trgtNode.firstChild.setAttribute('stroke-width', _oldTrgtNodeStrokeWidth);
            _trgtNode.firstChild.setAttribute('stroke', _oldTrgtNodeStrokeColor);
            _vLine.setAttribute('stroke-width', _oldLineStrokeWidth);
            _vLine.setAttribute('stroke', _oldLineStrokeColor);
            _hLine.setAttribute('stroke-width', _oldLineStrokeWidth);
            _hLine.setAttribute('stroke', _oldLineStrokeColor);
        }, 5000);
    }

};

export function onHover(node) {
    return function () {
            const gAttributes = {style:"cursor:pointer;",class:"hover"};
            for(let attr in gAttributes){
                this.oGHover.setAttribute(attr,gAttributes[attr]);
            }
    
            var oHoverText = document.createElementNS(this.svgNs,'text');
            const hoverTextAttributes = {x:node.co_ordinates.X,y:node.co_ordinates.Y - 30,"font-size":_CONSTANTS.TEXT.FONT_SIZE,fill:_CONSTANTS.TEXT.FONT_COLOR,'text-anchor':'middle',"font-family": "monospace"};
            for(let attr in hoverTextAttributes){
                oHoverText.setAttribute(attr,hoverTextAttributes[attr]);
            }
            oHoverText.innerHTML = node.text_value;
            if (node.text_value_new) {
                oHoverText.innerHTML = node.text_value + ' >> ' + node.text_value_new;
            }
            
    
            var oRect = document.createElementNS(this.svgNs,'rect');
            const rectAttributes = {rx:"10",fill:"white","stroke":"black","stroke-width":"1"};
            for(let attr in rectAttributes){
                oRect.setAttribute(attr,rectAttributes[attr]);
            }
    
            this.oGHover.appendChild(oRect);
            this.oGHover.appendChild(oHoverText);
            this.oGHover.onmouseleave = this.onHoverExit;
    
            const oHoverTextSize = oHoverText.getBBox();
            oRect.setAttribute('width',oHoverTextSize.width+10);
            oRect.setAttribute('height',oHoverTextSize.height+10);
            oRect.setAttribute('x',oHoverTextSize.x-5);
            oRect.setAttribute('y',oHoverTextSize.y-5);
    }
};

export function onHoverExit() {
    const elem = document.getElementsByClassName('hover');
    while (elem[0].hasChildNodes()) {
        elem[0].removeChild(elem[0].firstChild);
    }
};
