import {_CONSTANTS} from './../constants';
import {clone} from './../utility'

const node = {
    loc_id: '',
    type: '',
    text_value: '',
    text_display_value: null,
    co_ordinates: {X: 0, Y: 0},
    expanded: true,
    visible: true,
    children: []
}

export function constructGraph (data) {
    const ALL_NODES = {};

    const recurse = (data, parent_node) => {
        const _node = clone(node);
        if (!parent_node) {
            _node.loc_id = '0';
            _node.type = _CONSTANTS.NODE_TYPES.OBJECT;
            _node.text_value = _CONSTANTS.NODE_TYPES.ROOT_OBJECT;
            ALL_NODES[_node.loc_id] = _node;
            recurse(data, _node)
            return _node;
        } else {
            if (typeof data !== 'object' || data === null) { // print value
                const _node = clone(node);
                _node.loc_id = parent_node.loc_id + '.0';
                _node.type = _CONSTANTS.NODE_TYPES.END_VALUE;
                _node.text_value = data;
                ALL_NODES[_node.loc_id] = _node;
                parent_node.children.push(_node.loc_id);
    
            } else {
                Object.keys(data).forEach((key, idx) => {
                    const _node = clone(node);
                    _node.loc_id = parent_node.loc_id + '.' + idx;
                    _node.text_value = key;
                    if (typeof data[key] !== 'object' || data[key] === null) { // print key
                        _node.type = _CONSTANTS.NODE_TYPES.KEY;
                    } else { // print object
                        _node.type = Array.isArray(data[key]) ? _CONSTANTS.NODE_TYPES.ARRAY : _CONSTANTS.NODE_TYPES.OBJECT;
                    }
                    ALL_NODES[_node.loc_id] = _node;
                    parent_node.children.push(_node.loc_id);
                    recurse(data[key], _node);
                });
            }
        }
    }
    recurse(data);
    locateGraphNodes(ALL_NODES[0], ALL_NODES)
    return ALL_NODES;
};



export const locateGraphNodes = (node, ALL_NODES) => {
    let current_Y = 0;

    Object.values(ALL_NODES).forEach(node => {
        node.co_ordinates = {X: 0, Y: 0};
    })

    const findCoords = node => {
        if (!node.visible || node.co_ordinates.Y > 0) {
            //do not determine its coordinates
        } else if (node.type === 'End_Value') {
            let X;// = _level * _CONSTANTS.X_OFFSET;
            const Y = current_Y;
            node.co_ordinates = {X, Y};
        } else {
            const _level = node.loc_id.split('.').length;
            const X = _level * _CONSTANTS.X_OFFSET;
            const Y = current_Y + _CONSTANTS.Y_OFFSET;
            node.co_ordinates = {X, Y};
            current_Y = Y;
        }


        node.expanded === true && node.children.length > 0 && node.children.map(_id => {
            const _node = ALL_NODES[_id]
            findCoords(_node);
        })
    }

    findCoords(ALL_NODES[0]);
};


export function hideUnchanged (ALL_NODES_COMPARED, action) {
    const {UNCHANGED, MODIFIED} = _CONSTANTS.NODE_COMPARE_RESULT;

    if (action === 'ALL') {
        Object.values(ALL_NODES_COMPARED).forEach(node => {
            node.visible = true;
        })
        locateGraphNodes(ALL_NODES_COMPARED[0], ALL_NODES_COMPARED);
        return ALL_NODES_COMPARED;
    }

    Object.values(ALL_NODES_COMPARED).forEach(node => {
        node.visible = false;
    })

    const isUnchanged = id => ALL_NODES_COMPARED[id].compareStatus === UNCHANGED || !ALL_NODES_COMPARED[id].compareStatus;

    const processParent = node => {
        if (node.children.length === 0) {
            return isUnchanged(node.loc_id) ? UNCHANGED : MODIFIED;
        } else {
            let childrenModified = false;
            const MODIFIED_children = [];
            node.children.forEach((_nodeId, i) => {
                const childrenStatus = processParent(ALL_NODES_COMPARED[_nodeId]);
                if (childrenStatus === UNCHANGED) {

                } else {
                    MODIFIED_children.push(_nodeId);
                    childrenModified = true;
                }
            });
            MODIFIED_children.forEach(id => {
                ALL_NODES_COMPARED[id].visible = true;
            });
            if (childrenModified) {
                node.visible = true;
            }
            return !isUnchanged(node.loc_id) || childrenModified ? MODIFIED : UNCHANGED;
        }
    }
    
    processParent(ALL_NODES_COMPARED[0]);
    locateGraphNodes(ALL_NODES_COMPARED[0], ALL_NODES_COMPARED);
    return ALL_NODES_COMPARED;
}