import React, {useState, useEffect} from "react";
import {_CONSTANTS} from '../constants'
import ContextMenu from "./ContextMenu";
import './../Styles/Node.css'

const NodeEndValue = ({node, allNodes, containerId, updateState}) => {
    const [menuVisible, setMenuVisibility] = useState(false);

    const handleClick = () => setMenuVisibility(false)

    useEffect(() => {
        document.addEventListener("click", handleClick);
        return () => {
          document.addEventListener("click", handleClick);
        };
      }, []);

    let coOrdinates = {};
    let endValueColor = 'black';

    endValueColor = _CONSTANTS.COLORS[node.compareStatus];

    coOrdinates.X = node.co_ordinates.X;
    coOrdinates.Y = node.co_ordinates.Y;

    const showMenu = e => {
        e.preventDefault();
        e.stopPropagation();
        setMenuVisibility(!menuVisible)
    }

    return (
        <span onContextMenu={(e) => showMenu(e)}>
            <span id={node.loc_id} style={{color:endValueColor}}>
                { [1, -1].includes(containerId) ? (node.modifiedValue || node.text_value) + '' : node.text_value + ''}
                {menuVisible && <ContextMenu {...{node, allNodes, containerId, updateState}} />}
            </span>
        </span> 
    )
}

export default NodeEndValue;