import React from "react";
import classNames from 'classnames';
import {_CONSTANTS} from './../constants';
import Node from './Node';
import './../Styles/Container.css'

const Container = ({state, updateState}) => {
    const {DELETED, ADDED} = _CONSTANTS.NODE_COMPARE_RESULT;
    const {SPLIT} = _CONSTANTS.COMPARE_TYPE;
    let allNodes;
    if (state.UI_STATE === 700) {
        const compareData = state.COMPARE_CACHE[state.ACTIVE_COMPARE_ID]
        allNodes = compareData.ALL_NODES_COMPARED;
    } else {
        allNodes = state?.JSON_SET?.[state.ACTIVE_JSON]?.GRAPH || {};
    }

    let textAreaValue;
    if (state.ACTIVE_JSON > -1 && state.UI_STATE !== 600) {
        textAreaValue = state.JSON_SET[state.ACTIVE_JSON].FORMATTED_JSON;
    }

    return (
            state.UI_STATE === 700 && state.COMPARE_TYPE === SPLIT ? (
                <>
                    <div className={classNames("container", "container-split", "container-left")}>
                        <span className='container-title'>{state?.JSON_SET?.[state.ACTIVE_JSON]?.TITLE} Elastic Search</span>
                        {Object.values(allNodes).map(node => {
                            return node.compareStatus !== ADDED && <Node key={node.loc_id} node={node} allNodes={allNodes} containerId={0} updateState={updateState}/>
                        })}
                    </div>
                    <div className={classNames("container", "container-split", "container-right")}>
                        <span className='container-title'>{state?.JSON_SET?.[state.COMPARE_INDEX]?.TITLE} Couch Base</span>
                        {Object.values(allNodes).map(node => {
                            return node.compareStatus !== DELETED &&  <Node key={node.loc_id} node={node} allNodes={allNodes} containerId={1} updateState={updateState} />
                        })}
                    </div>
                </>
            ) : (
                <div className="container">
                {/* 12 */}
                    {[100, 200, 600, 400].includes(state.UI_STATE) && <textarea id="jsonTemplate" name="jsonTemplate" placeholder="Provide a valid JSON..." value={textAreaValue}></textarea>}
                    {![100, 200, 600, 400].includes(state.UI_STATE) && Object.values(allNodes).map(node => {
                        return <Node key={node.loc_id} node={node} allNodes={allNodes} updateState={updateState} containerId={-1}/>
                    })}
                </div>
            )
    )
}

export default Container;