import Header from './components/Header'
import Container from './components/Container';
import './App.css';
import React, { useEffect, useState } from "react";
import {reducer} from './controller/Reducer'
import {validateJson, formatJson} from './utility';
import {constructGraph} from './graphLib/graph'
import json1 from './data/json1.json'
import json2 from './data/json2.json'
import {compareHandler} from './controller/ActionHandler'

const defaultState = {
  UI_STATE: 100,
  JSON_SET: {
/*         ID: {
          RAW_JSON_STRING: '',
          FORMATTED_JSON: undefined,
          GRAPH: undefined
          TITLE: undefined
      } */
  },
  ACTIVE_JSON: -1,
  COMPARE_INDEX: -1,
  ACTIVE_COMPARE_ID: false,
  COMPARE_CACHE: {},
  COMPARE_TYPE: 'SPLIT', //'UNIFIED',
  COMPARE_DISPLAY_DIFFERENCE_TYPE: 'ALL', //'HIDE'
  SELECTED_NODE: undefined,
}

const initializeApp = () => {
  let first, second
  const queryString = window.location.search;
  if (queryString) {
     first = JSON.stringify(json1); //localStorage.getItem("json1");
     second = JSON.stringify(json2); //localStorage.getItem("json2");
     if (first && second) {
      return Promise.resolve({first, second})
     }
  }
  return Promise.resolve({})
}

function App() {
  const [state, setNewState] = useState(defaultState);

  localStorage.setItem('json1', JSON.stringify({"a": 1}))
  localStorage.setItem('json2', JSON.stringify({"a": 2}))

  const StateController = () => {
      const getState = () => state;
      const setState = (action, args) => {
          const newState = reducer(action, state, args)
          if (state.UI_STATE !== newState.UI_STATE) {
            newState.PREVIOUS_STATE = state.UI_STATE;
          }
          setNewState(newState)
          return newState;
      };
  
      return {getState, setState}
  }

  const {getState, setState} = StateController();


  useEffect(() => {
    initializeApp().then(res => {
      const {first, second} = res;
      const newState = JSON.parse(JSON.stringify(getState()))
      if (first && second) {
        const _isValid = validateJson(first) && validateJson(second);
        if (_isValid) {
            const {JSON_SET} = newState;
            JSON_SET[0] = {};
            JSON_SET[0].RAW_JSON_STRING = first;
            JSON_SET[0].FORMATTED_JSON = formatJson(first);
            JSON_SET[0].GRAPH = constructGraph(JSON.parse(first));
            JSON_SET[1] = {};
            JSON_SET[1].RAW_JSON_STRING = second;
            JSON_SET[1].FORMATTED_JSON = formatJson(second);
            JSON_SET[1].GRAPH = constructGraph(JSON.parse(second));
            newState.ACTIVE_JSON = 0;
            newState.COMPARE_INDEX = 1;
            newState.PREVIOUS_STATE = 500
            newState.UI_STATE = 700;
            const _state = compareHandler(newState, {});

            setNewState(_state);
        } else {
            alert(" Invalid JSON ")
        }
      }
    });
  }, []);

  return (
    <div className="App">
      <Header state={getState()} updateState={setState}/>
      <Container state={getState()} updateState={setState}/>
    </div>
  );
};

export default App;
