import React from "react";
import './../Styles/Connector.css'

const Connector = ({coOrdinates1, coOrdinates2}) => {
    return (
        <span className="connector" style={{
            left: coOrdinates1.X + 3, width: coOrdinates2.X - coOrdinates1.X - 6, top: coOrdinates1.Y + 17, height: coOrdinates2.Y - coOrdinates1.Y - 7}}></span>

    )
}

export default Connector;