import {_CONSTANTS} from './../constants.js';
import {clone} from './../utility.js';
import isEqual from 'lodash.isequal';
import {locateGraphNodes} from './graph'

/**
 * ### Assumptions and Considerations in Comparison ###
 * Only Strict equality check (===) in primitive value comparison
 * Objects inside Array are not DEEP compared for differencea. They are  either equal (UNCHANGED)  or  Completed different, and then - 
 *  * Object in source is marked as DELETED
 *  * Object in destination is marked as ADDED
 */

const isPrimitive = val => !(val === Object(val));

export function compareGraphs(compareFirst, compareSecond) {
    const {graph: g1, ALL_NODES: A_N_1, data: d1} = compareFirst;
    const {graph: g2, ALL_NODES: A_N_2, data: d2} = compareSecond;
    const {UNCHANGED, MODIFIED, ADDED, DELETED} = _CONSTANTS.NODE_COMPARE_RESULT
    const data1 = JSON.parse(d1);
    const data2 = JSON.parse(d2);
    const graph_compared = g1;
    const ALL_NODES_COMPARED = clone(A_N_1);

    const updateNodeStatus = (key, status, modifiedNode) => {
        ALL_NODES_COMPARED[key].compareStatus = status;
        if (status === MODIFIED) {
            const node = ALL_NODES_COMPARED[key];
            node.modifiedValue = modifiedNode.text_value || modifiedNode;
        }
        if (status === DELETED) {
            const node = ALL_NODES_COMPARED[key];
            node.children.forEach(id => updateNodeStatus(id, DELETED));
        }
    }

    const updatePrefix = (oldId, newPrefix) => {
        const a = oldId.split('.');
        const b = newPrefix.split('.');
        a.splice(0, b.length, ...b);
        return a.join('.');
    }

    const addAndUpdateChildNodes = (node, prefix, root) => {
        node.children.forEach((_child, idx) => {
            const _n = clone(A_N_2[_child]);
            const _newKey = updatePrefix(_n.loc_id, prefix);
            _n.loc_id = _newKey;
            _n.compareStatus = ADDED;
            root[_newKey] = _n;
            node.children[idx] = _newKey;
            addAndUpdateChildNodes(_n, _newKey, root);
        })
    }

    const addNode = (node, status, prefix, path2) => {
        const nodeToAdd = clone(A_N_2[path2]);
        nodeToAdd.compareStatus = status
        const allIdsAtLevel = Object.keys(ALL_NODES_COMPARED).filter(key => key.length === prefix.length + 2 && key.startsWith(prefix));
        const allSuffixes = allIdsAtLevel.map(i => {
            return parseInt(i.split('.').slice(-1));
        });
        const newSuffix = Math.max(...allSuffixes) + 1;
        const newKey = prefix + '.' + newSuffix;

        nodeToAdd.loc_id = newKey;
        ALL_NODES_COMPARED[newKey] = nodeToAdd;
        ALL_NODES_COMPARED[prefix].children.push(newKey);
        if (nodeToAdd.children.length > 0) {
            addAndUpdateChildNodes(nodeToAdd, newKey, ALL_NODES_COMPARED);
        }
    }

    const compareNode = (node1, node2, path1, path2) => {
        if (isEqual(node1, node2)) {
            //both objects/arrays are equal
            updateNodeStatus(path1, UNCHANGED);
            return;
        }
        if (/* Array.isArray(node1) && Array.isArray(node2) */false) {
            const nonPrimitivesInSource = [];
            node1.forEach((elem, idx) => {
                if (isPrimitive(elem)) { //only primitive values are compared (string,number,bigint,boolean,undefined,symbol,null)
                    const found = node2.indexOf(elem);
                    if (found >= 0) {
                        updateNodeStatus(path1 + '.' + idx, UNCHANGED);
                        node2.splice(found, 1, "NODE_FOUND_XXXX");
                    } else {
                        updateNodeStatus(path1 + '.' + idx, DELETED);
                    }
                } else {
                    nonPrimitivesInSource.push({elem, idx});
                }

            });
            nonPrimitivesInSource.forEach(({elem, idx}) => { //Comparing non-primitives only
                let found;
                node2.forEach((_elem2, _idx2) => {
                    if (!found && !isPrimitive(_elem2) && isEqual(elem, _elem2)) {
                        updateNodeStatus(path1 + '.' + idx, UNCHANGED);
                        node2.splice(_idx2, 1, "NODE_FOUND_XXXX");
                        found = true;
                    }
                });
                !found && updateNodeStatus(path1 + '.' + idx, DELETED);
            })
            node2.forEach((elem, idx) => {
                //add elem
                if (elem !== 'NODE_FOUND_XXXX') {
                    addNode(elem, ADDED, path1, path2 + '.' + idx);
                }
            })
        } else if(typeof node1 === 'object' && typeof node1 === typeof node2) {
            const node1Keys = Object.keys(node1);
            const node2Keys = Object.keys(node2);
            node1Keys.forEach((key, idx) => {
                if (node2.hasOwnProperty(key)) {
                    let newPath1 = path1;
                    let newPath2 = path2;
                    newPath1 = path1 + '.' + idx;
                    newPath2 = path2 + '.' + node2Keys.indexOf(key);
                    compareNode(node1[key], node2[key], newPath1, newPath2);
                } else {
                    updateNodeStatus(path1 + '.' + idx, DELETED);
                }
            });
            node2Keys.forEach((key, idx) => {
                if (!node1Keys.includes(key)) {
                    //add elem node2[key] with status ADDED
                    addNode(key, ADDED, path1, path2 + '.' + idx);
                }
            });
        } else {
            if (isPrimitive(node1)) {
                //if node1 is END_VALUE, then - path1 will denote KEY node. Update status of children of this KEY i.e. END_VALUE
                updateNodeStatus(path1 + '.0', MODIFIED, node2);
            } else {
                updateNodeStatus(path1, MODIFIED);
            }
        };
    }

    compareNode(data1, data2, '0', '0');
    locateGraphNodes(ALL_NODES_COMPARED[0], ALL_NODES_COMPARED);
    return {graph_compared, ALL_NODES_COMPARED}
}


export function hideUnchanged (graph_compared, ALL_NODES_COMPARED) {
    const {UNCHANGED, MODIFIED} = _CONSTANTS.NODE_COMPARE_RESULT;

    const isUnchanged = id => ALL_NODES_COMPARED[id].compareStatus === UNCHANGED || !ALL_NODES_COMPARED[id].compareStatus;

    const processParent = node => {
        if (node.children.length === 0) {
            return isUnchanged(node.loc_id) ? UNCHANGED : MODIFIED;
        } else {
            let childrenModified = false;
            const MODIFIED_children = [];
            node.children.forEach((_nodeId, i) => {
                const childrenStatus = processParent(ALL_NODES_COMPARED[_nodeId]);
                if (childrenStatus === UNCHANGED) {

                } else {
                    MODIFIED_children.push(_nodeId);
                    childrenModified = true;
                }
            });
            const nodesToDleete = node.children.filter(id => !MODIFIED_children.includes(id));
            nodesToDleete.forEach(id => delete ALL_NODES_COMPARED[id]);
            node.children = MODIFIED_children;
            return !isUnchanged(node.loc_id) || childrenModified ? MODIFIED : UNCHANGED;
        }
    }
    
    processParent(graph_compared);
    return {graph_modified: graph_compared, ALL_MODIFIED_NODES: ALL_NODES_COMPARED}
}
    
