export const readUIFielObjectById = id => document.getElementById(id) || {};

export const setUIFieldDisplay = (id, display) => document.getElementById(id).style.display = display;

export const disableUIField = id => document.getElementById(id).disabled = true;

export const clone = data => JSON.parse(JSON.stringify(data));

export const creatSvgTage = (tage, json) => {
    const svgNs = "http://www.w3.org/2000/svg";
    var oTage = document.createElementNS(svgNs,tage);
    for(var attr in json){
        oTage.setAttribute(attr,json[attr]);
    }
    return oTage;
};

export const validateJson = str => {
    try {
        return (JSON.parse(str) && !!str);
    } catch (e) {
        return false;
    }
};

export const formatJson = json => {
    return JSON.stringify(JSON.parse(json), null, 2);
};