import React, {useState, useEffect} from "react";
import classNames from 'classnames';
import {_CONSTANTS} from './../constants'
import Connector from './Connector';
import NodeEndValue from './NodeEndValue';
import ContextMenu from "./ContextMenu";
import './../Styles/Node.css'

const Node = ({node, allNodes, containerId, updateState}) => {
    const [menuVisible, setMenuVisibility] = useState(false);

    const handleClick = () => setMenuVisibility(false)

    const {NODE_TYPES: {KEY, END_VALUE}} = _CONSTANTS;

    useEffect(() => {
        document.addEventListener("click", handleClick);
        return () => {
          document.addEventListener("click", handleClick);
        };
      }, []);

    if (node.co_ordinates.X === 0 && node.co_ordinates.Y === 0) {
        return null;
    }
    let endValueNode, parentNode, coOrdinates = {};
    let color = 'black', endValueColor = 'black';
    if (node.compareStatus) {
        color = _CONSTANTS.COLORS[node.compareStatus];
    }
    if (node.type === KEY) {

        endValueNode = allNodes[node.children[0]];
        if (endValueNode.compareStatus) {
            endValueColor = _CONSTANTS.COLORS[endValueNode.compareStatus];
        }
    } else if (node.type === END_VALUE) {
        return null;
    } else {

    }
    coOrdinates.X = node.co_ordinates.X;
    coOrdinates.Y = node.co_ordinates.Y;
    if (node.loc_id.split('.').length > 1) {
        const idArray = node.loc_id.split('.')
        idArray.pop();
        const parentId = idArray.join('.');
        parentNode = allNodes[parentId];
    }

    const showMenu = e => {
        e.preventDefault();
        setMenuVisibility(!menuVisible)
    }

    return (
        <>
            <span className={classNames('node', {'node-collapsed': node.expanded === false})} style={{left: coOrdinates.X, top: coOrdinates.Y}} onContextMenu={(e) => showMenu(e)}>
                {
                    node.type === KEY 
                    ? <span className={'nodeValue'}>
                        <span id={node.loc_id} style={{color:color}}>{node.text_value}</span>  :  <NodeEndValue node={endValueNode} {...{allNodes, containerId, updateState}}/>
                    </span>
                    : <span  className={'nodeValue'} id={node.loc_id} style={{color}}>{node.text_value}</span>
                }
                {menuVisible && <ContextMenu {...{node, allNodes, containerId, updateState}} />}
            </span>
            {parentNode && <Connector coOrdinates1={parentNode.co_ordinates} coOrdinates2={node.co_ordinates} />}
        </>
        
    )
}

export default Node;