import {submitHandler, visualizeJsonHandler, viewJsonHandler, addAnotherJsonHandler, cancelHandler, compareHandler, clearCompareHandler, changeActiveJsonHandler, changeCompareJsonHandler, changeCompareDisplayTypeHandler, changeCompareDisplayDifferenceTypeHandler} from './ActionHandler'
import {collapseAllSiblings, collapseAllChild, expandCollapseNode, focusParent, pathToRoot, highlightConnectedNodes} from './NodeEventHandlers';

export const reducer = (action, currentState, args) => {
    const state = JSON.parse(JSON.stringify(currentState))
    switch (action) {
        case 'submit':
            return submitHandler(state, args);
        case 'visualizeJson':
            return visualizeJsonHandler(state, args);
        case 'viewJson':
            return viewJsonHandler(state, args);
        case 'addAnotherJson':
            return addAnotherJsonHandler(state, args);
        case 'cancel':
            return cancelHandler(state, args);
        case 'compare':
            return compareHandler(state, args);
        case 'clearCompare':
            return clearCompareHandler(state, args);
        case 'changeActiveJson':
            return changeActiveJsonHandler(state, args);
        case 'changeCompareJson':
            return changeCompareJsonHandler(state, args);
        case 'changeCompareDisplayType':
            return changeCompareDisplayTypeHandler(state, args);
        case 'changeCompareDisplayDifferenceType':
            return changeCompareDisplayDifferenceTypeHandler(state, args);

        case 'collapseAllSiblings':
            return collapseAllSiblings(state, args);
        case 'collapseAllChild':
            return collapseAllChild(state, args);
        case 'expandCollapseNode':
            return expandCollapseNode(state, args);
        case 'focusParent':
            return focusParent(state, args);
        case 'pathToRoot':
            return pathToRoot(state, args);
        case 'highlightConnectedNodes':
            return highlightConnectedNodes(state, args);
        default:
    }
}