import React from "react";
import './../Styles/Header.css'

const Header = ({state = {}, updateState}) => {

    const compareIndices = Object.keys(state.JSON_SET).map(key => {
        if (key === state.ACTIVE_JSON) {
            return null;
        }
        return key;
    });
    if (compareIndices.length === 1) {
        //updateState('changeCompareJson', {value: compareIndices[0]})
    }

    return <div className={'header'}>
        {/* 1 */}
        {[200, 400].includes(state.UI_STATE) && <button id="visualizeJSON" onClick={() => updateState('visualizeJson', {})}>Visualize JSON</button>}
        {/* 2 */}

        {/* 3 */}
        {[200, 300, 400, 500].includes(state.UI_STATE) && <button id="addAnotherJson" onClick={() => updateState('addAnotherJson', {})}>Add JSON</button>}
        {/* 4 */}
        {[400, 500].includes(state.UI_STATE) && <select name="activeJson" id="activeJson" value={state.ACTIVE_JSON} onChange={e => updateState('changeActiveJson', {value: e.target.value})}>
            {Object.keys(state.JSON_SET).map(key => {
                return <option value={key}>JSON {key}</option>
            })}
        </select>}
        {/* 5 */}
        {[500].includes(state.UI_STATE) && <select name="compareWith" id="compareWith" value={state.COMPARE_INDEX} onChange={e => updateState('changeCompareJson', {value: e.target.value})}>
            {compareIndices.map(key => {
                return <option value={key}>JSON {key}</option>
            })}
        </select>}
        {/* 6 */}
        {[700].includes(state.UI_STATE) && 
            <span id="compareDisplayType">
                <input type="radio" name="compareDisplayType" value="UNIFIED" id="UNIFIED" checked={state.COMPARE_TYPE === "UNIFIED"} onChange={e => updateState('changeCompareDisplayType', {value: e.target.value})}/>
                    <label htmlFor="UNIFIED">Unified</label>
                &nbsp;&nbsp;&nbsp;
                <input type="radio" name="compareDisplayType" value="SPLIT" id="SPLIT" checked={state.COMPARE_TYPE === "SPLIT"} onChange={e => updateState('changeCompareDisplayType', {value: e.target.value})}/>
                    <label htmlFor="SPLIT">Split</label>
            </span>
        }

        {/* 7 */}
        {[700].includes(state.UI_STATE) && 
        <span id="CompareShowAllOrUnchanged">
            <input type="radio" name="CompareShowAllOrUnchanged" value="ALL" id="ALL" checked={state.COMPARE_DISPLAY_DIFFERENCE_TYPE === "ALL"} onChange={e => updateState('changeCompareDisplayDifferenceType', {value: e.target.value})}/>
                <label htmlFor="UNIFIED">Show All</label>
                &nbsp;&nbsp;&nbsp;
            <input type="radio" name="CompareShowAllOrUnchanged" value="HIDE" id="HIDE" checked={state.COMPARE_DISPLAY_DIFFERENCE_TYPE === "HIDE"} onChange={e => updateState('changeCompareDisplayDifferenceType', {value: e.target.value})}/>
                <label htmlFor="SPLIT">Hide Unchanged</label>
        </span>
        }
        {/* 8 */}
{/*         <select name="shallow" id="shallow">
            <option value="ALL">Show All</option>
            <option value="HIDE">Hide Unchanged</option>
        </select> */}
        {/* 9 */}
        {[500].includes(state.UI_STATE) && <button id="compareJson" onClick={() => updateState('compare', {})}>Compare</button>}
        {/* 10 */}
        {[700].includes(state.UI_STATE) && <button id="clearCompare" onClick={() => updateState('clearCompare', {})}>Clear Compare</button>}
        {/* 11 */}
{/*         {[100, 200, 600, 400].includes(state.UI_STATE) && <select name="selectView" id="selectView">
            <option value="graph">Graph</option>
            <option value="json">JSON</option>
        </select>} */}

        {/* 13 */}
        {/* Graphic container */}
        {/* 14 */}
        {[600].includes(state.UI_STATE) && <button id="cancel" onClick={() => updateState('cancel', {})}>Cancel</button>}
        {/* 15 */}
        {[100, 600].includes(state.UI_STATE) && <button id="submit" onClick={() => updateState('submit', {})}>Submit</button>}
        {/* 16 */}
        {[300].includes(state.UI_STATE) && <button id="viewJson" onClick={() => updateState('viewJson', {})}>View JSON</button>}
    </div>
}

export default Header;