import {readUIFielObjectById, validateJson, formatJson} from './../utility';
import {constructGraph, hideUnchanged} from './../graphLib/graph'
import {compareGraphs} from './../graphLib/comparator'

export const submitHandler = (state, args) => {
    const _rawJson = args.rawJson ? args.rawJson : readUIFielObjectById('jsonTemplate').value;
    const _isValid = validateJson(_rawJson);
    if (_isValid) {
        const {JSON_SET} = state;
        const id = Object.keys(JSON_SET).length;
        JSON_SET[id] = {};
        JSON_SET[id].RAW_JSON_STRING = _rawJson;
        JSON_SET[id].FORMATTED_JSON = formatJson(_rawJson);
        if (!args.rawJson) {
            readUIFielObjectById('jsonTemplate').value = JSON_SET[id].FORMATTED_JSON;
        }
        state.ACTIVE_JSON = id;
        state.UI_STATE = state.UI_STATE === 100 ? 200 : 400;
    } else {
        alert(" Invalid JSON ")
    }
    return state;
}

export const visualizeJsonHandler = (state, args) => {
    let newState;
    const activeJsonId = state.ACTIVE_JSON;
    const rawJson = state.JSON_SET[activeJsonId]?.RAW_JSON_STRING
    const graph = constructGraph(JSON.parse(rawJson || {}));
    state.JSON_SET[activeJsonId].GRAPH = graph;
    if (state.UI_STATE === 200) {
        newState = 300
    } else if (state.UI_STATE === 400) {
        newState = 500;
    }
    state.UI_STATE = newState;
    return state;
}

export const viewJsonHandler = (state, args) => {
    state.UI_STATE = 200;
    return state;
}

export const addAnotherJsonHandler = (state, args) => {
    state.UI_STATE = 600;
    state.ACTIVE_JSON = -1;
    return state;
}

export const cancelHandler = (state, args) => {
    state.UI_STATE = state.PREVIOUS_STATE;
    return state;
}

export const compareHandler = (state, args) => {
    const compareFirst = state.JSON_SET[state.ACTIVE_JSON];
    const compareSecond = state.JSON_SET[state.COMPARE_INDEX];
    const firstData = {
        graph: compareFirst.GRAPH[0],
        ALL_NODES: compareFirst.GRAPH,
        data: compareFirst.RAW_JSON_STRING
    };
    const secondData = {
        graph: compareSecond.GRAPH[0],
        ALL_NODES: compareSecond.GRAPH,
        data: compareSecond.RAW_JSON_STRING
    };
    const comparedData = compareGraphs(firstData, secondData);
    const compareIndex = state.ACTIVE_JSON + '__' + state.COMPARE_INDEX;
    state.COMPARE_CACHE[compareIndex] = comparedData;
    state.ACTIVE_COMPARE_ID = compareIndex;
    state.UI_STATE = 700;
    return state;
}

export const clearCompareHandler = (state, args) => {
    state.COMPARE_INDEX = -1;
    state.ACTIVE_JSON = Object.keys(state.JSON_SET)[0]; 
    state.UI_STATE = state.PREVIOUS_STATE;
    return state;
}

export const changeActiveJsonHandler = (state, args) => {
    state.ACTIVE_JSON = parseInt(args.value);
    return state
}

export const changeCompareJsonHandler = (state, args) => {
    state.COMPARE_INDEX = parseInt(args.value);
    return state;
}

export const changeCompareDisplayTypeHandler = (state, args) => {
    state.COMPARE_TYPE = args.value;
    return state;
}

export const changeCompareDisplayDifferenceTypeHandler = (state, args) => {
    const allNodes = state.COMPARE_CACHE['0__1'].ALL_NODES_COMPARED;
    hideUnchanged(allNodes, args.value)
    state.COMPARE_DISPLAY_DIFFERENCE_TYPE = args.value;
    return state;
}