import React from "react";
import {_CONSTANTS} from './../constants'
import './../Styles/ContextMenu.css';

const ContextMenu = ({node, allNodes, containerId, updateState}) => {

    const {NODE_TYPES: {KEY, ROOT_OBJECT}} = _CONSTANTS;

    const copyNodeText = () => {
        navigator.clipboard.writeText(node.text_value)
    }


    return (
        <>
        <div class="triangle"></div>
        <div id="context-menu">
            {node.type !== KEY && <div className={'item'} id="collapseThis" onClick={() => updateState('expandCollapseNode', {node, allNodes})}>Collapse/Expand this node</div>}
            <div className={'item'} id="collapseAllSiblings" onClick={() => updateState('collapseAllSiblings', {node, allNodes})}>Collapse all siblings</div>
            {/* <!-- <div class="item" id="expandAllSiblings">Expand all siblings</div> --> */}
            <div className={'item'} id="copytext" onClick={copyNodeText}>Copy Text</div>
            <div className={'item'} id="focusParent" onclick="_.nodeClickHandler('focusParent')">Go To Parent</div>
            {node.type !== KEY && <div className={'item'} id="collapseAllChild" onclick="_.nodeClickHandler('collapseAllChild')">Collapse All Child Objects</div>}
            {node.type !== ROOT_OBJECT && <div className={'item'} id="pathToRoot">Path to Root</div>}
        </div>
        </>

    )
}

export default ContextMenu;